import { AfterViewInit, Component, ElementRef, ViewChild } from '@angular/core';
import { TestService } from '../test.service';
import { TimelineMax ,Linear , Draggable,Elastic,Expo,TweenMax} from 'gsap/all';
import { gsap } from 'gsap/all';
import { NavigationEnd, Router } from '@angular/router';
gsap.registerPlugin(Draggable);
gsap.registerPlugin(TimelineMax);
gsap.registerPlugin(Linear);
gsap.registerPlugin(Elastic);
gsap.registerPlugin(TweenMax);
@Component({
  selector: 'app-tab3',
  templateUrl: 'tab3.page.html',
  styleUrls: ['tab3.page.scss']
})
export class Tab3Page implements AfterViewInit{

  regadores 
  humedad
  ambiente
  dragger
  regador
  showError
  showMessage = false
  frasco = false
  @ViewChild('draggerel') draggerel:ElementRef;
  constructor(private testService : TestService, private router: Router) {
    router.events.subscribe((val) => {
      if(val instanceof NavigationEnd){
    //  this.getData()
    
     this.getHumedad()

      }
  });
}
  ngAfterViewInit(): void {
    
  }
  ngOnInit(): void {
    // this.ultimasdiez = JSON.parse(this.getData2())
    // console.log(this.ultimasdiez)
    this.ambiente = localStorage.getItem('ambienteseleccionado');

    // this.getData()
    this.getHumedad()
    
  }

  getData(){
    this.ambiente = localStorage.getItem('ambienteseleccionado');
    this.testService.getRegadores(this.ambiente).subscribe(
      res => {
        console.log(res)
        this.regador = res[0]
        
        if(res.hasOwnProperty('error')){
          this.showMessage = true
          console.log("ERROR")
          }
      },
      error => {
        console.log(error)
      }
    )

  }
  getHumedad(){
    this.ambiente = localStorage.getItem('ambienteseleccionado');
    this.testService.getHumedad(this.ambiente).subscribe(
      res => {
        console.log(res[0])
        this.humedad = res[0]
    // this.drawHumidtySensor(this.humedad.value *-1)

      },
      error => {
        console.log(error)
      }
    )
  }
  toggleRegador(regador){
    
  }
  drawHumidtySensor(valorHumedad){
    var xmlns = "http://www.w3.org/2000/svg",
    xlinkns = "http://www.w3.org/1999/xlink",
    select = function(s) {
      return document.querySelector(s);
    },
    selectAll = function(s) {
      return document.querySelectorAll(s);
    },
      liquid = selectAll('.liquid'),
      tubeShine = select('.tubeShine'),
      label = select('.label'),
      follower = select('.follower'),
      dragger = select('.dragger'),
      dragTip = select('.dragTip'),
      minDragY = -380,
      liquidId = 0,
      step = Math.abs(minDragY/100),
      snap = valorHumedad,
      followerVY = 0
    
  
  TweenMax.set('svg', {
    visibility: 'visible'
  })
  
  TweenMax.set(dragTip, {
   transformOrigin:'20% 50%'
  })
  
  var tl = new TimelineMax()
  tl.staggerTo(liquid, 0.9, {
   x:'-=200',
   ease:Linear.easeNone,
   repeat:-1
  },0.9)
  
  tl.time(100);

  TweenMax.to(liquid, 1.3, {
    y:valorHumedad,
    ease:Elastic.easeOut.config(1,0.4)
   })

  TweenMax.to(dragger, 1.4, {
   y:valorHumedad,
  
   ease:Expo.easeInOut
  })
  
  
  //ScrubGSAPTimeline(tl);
  
  
  }
}
  